elo; de esta manera genera todas las condiciones favorables para aumentar la diversidad de las especies, sobre todo las vegetales. En este punto resulta importante anotar que la diversidad se expresa en número de especies por área; la flora silvestre puede ser cuantificada y ser un factor determinante en el estado de conservación de un ecosistema.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mb-5",attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img02.svg")}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cajon-b color-acento-contenido p-3 mb-4",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Existen diferentes relaciones de parentesco entre las plantas; estas relaciones permiten agrupar y clasificar cada una de las especies según las similitudes o diferencias que suelen ser de tipo morfológico, cariológico, fitogeográfico, genético, entre otros aspectos que se tienen en cuenta en este proceso.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"BGIMG04 p-4 mb-4"},[_c('div',{staticClass:"row justify-content-center align-items-end"},[_c('div',{staticClass:"col-lg-3 col-6 mb-lg-0 mb-4",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img05.svg")}})]),_c('div',{staticClass:"col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Es importante anotar que las plantas briofitas están representadas por tres líneas evolutivas en las que se encuentran los musgos, las hepáticas y los antoceros. Están divididas en 9 000 especies de musgos, 7 500 de hepáticas y 250 de antoceros (Morales et al, 2017). Su importancia ecológica radica en ser recolectoras de agua e indicadoras de pH, y su abundancia en los bosques es imprescindible para prevenir la erosión y generar esta